/**
* base.reset
* ------------------
*/
a, abbr, acronym, address, applet, article, aside, audio, b, big, blockquote, body, canvas, caption, center, cite, code, dd, del, details, dfn, div, dl, dt, em, embed, fieldset, figcaption, figure, footer, form, h1, h2, h3, h4, h5, h6, header, hgroup, html, i, iframe, img, ins, kbd, label, legend, li, mark, menu, nav, object, ol, output, p, pre, q, ruby, s, samp, section, small, span, strike, strong, sub, summary, sup, table, tbody, td, tfoot, th, thead, time, tr, tt, u, ul, var, video, input, textarea, select, button {
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  box-sizing: border-box;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:after, blockquote:before, q:after, q:before {
  content: "";
  content: none;
}

a {
  color: #535353;
  text-decoration: none;
  cursor: pointer;
}

button {
  border: none;
  background: none;
  cursor: pointer;
}

button span {
  position: relative;
}

table {
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
  table-layout: fixed;
  word-break: break-all;
}

/* Normalize ***************************************************/
body, html {
  height: 100%;
}

body {
  line-height: 24px;
  font-size: 15px;
  font-family: "Noto Sans", "Noto Sans", "Malgun Gothic", Dotum, Helvetica, "Apple SD Gothic Neo", Sans-serif;
  font-weight: 400;
  color: #000;
  letter-spacing: -0.7px;
  word-break: break-all;
  -webkit-text-size-adjust: none;
  text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

img {
  vertical-align: top;
  transform: translateZ(0);
  backface-visibility: hidden;
}

input[type=text]::-ms-clear {
  display: none;
}

legend, .offscr {
  position: absolute !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden !important;
  clip: rect(1px 1px 1px 1px) !important;
}

caption {
  margin: -1px;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden !important;
  clip: rect(1px 1px 1px 1px) !important;
}

input[type=text]::-ms-clear, input[type=tel]::-ms-clear {
  display: none;
}

::-webkit-input-placeholder {
  font-family: "Noto Sans";
  color: #888;
  opacity: 1;
}

:-moz-placeholder {
  font-family: "Noto Sans";
  color: #888;
  opacity: 1;
}

::-moz-placeholder {
  font-family: "Noto Sans";
  color: #888;
  opacity: 1;
}

:-ms-input-placeholder {
  font-family: "Noto Sans" !important;
  color: #888 !important;
  opacity: 1;
}

input[type=text]:focus::-webkit-input-placeholder {
  visibility: hidden;
}

input[type=text]:focus:-moz-placeholder {
  visibility: hidden;
}

input[type=text]:focus::-moz-placeholder {
  visibility: hidden;
}

input[type=text]:focus:-ms-input-placeholder {
  visibility: hidden;
}

select,
input[type=text],
input[type=password],
input[type=tel],
input[type=number],
input[type=file] {
  -webkit-appearance: none;
  border-radius: 0;
}

/**
* base.form
* ------------------
*/
.inp_txt, input[type=text],
input[type=password],
input[type=tel] {
  border: 1px solid #ccc;
  border-radius: 6px;
  padding: 0 15px 0 15px;
  caret-color: #ea7404;
  width: 100%;
  height: 46px;
  font-size: 16px;
  color: #000;
}

.inp_txt:focus,
input[type=text]:focus,
input[type=password]:focus,
input[type=tel]:focus {
  border-color: #ea7404;
  outline: none;
  box-shadow: 1px 3px 8px rgba(0, 0, 0, 0.09);
}

.inp_txt {
  padding: 0 40px 0 15px;
}

textarea:disabled,
select:disabled,
input:disabled {
  background-color: #f4f4f4;
}

/* select */
select {
  position: relative;
  width: 100%;
  height: 46px;
