/* CheckBox, Radio */
input[type=checkbox],
input[type=radio] {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  overflow: hidden;
  padding: 0;
  clip: rect(0, 0, 0, 0);
  -webkit-appearance: none;
  opacity: 0;
}

input[type=radio]:focus + label:before,
input[type=checkbox]:focus + label:before {
  box-shadow: 0px 0px 2px 2px rgba(0, 97, 255, 0.8);
}

input[type=radio] + label > .sub {
  display: inline-block;
  color: #777;
  margin-left: 10px;
}

input[type=checkbox].ss + label {
  font-size: 14px;
  color: #555;
  line-height: 20px;
  padding-left: 30px;
  min-height: 20px;
}

input[type=checkbox].ss + label::before {
  width: 20px;
  height: 20px;
  margin-top: -10px;
}

/* checkbox */
input[type=checkbox] + label {
  position: relative;
  display: inline-block;
  padding-left: 36px;
  font-size: 16px;
  min-height: 28px;
  color: #000;
  line-height: 26px;
  cursor: pointer;
}

input[type=checkbox] + label::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  width: 25px;
  height: 25px;
  border: 1px solid #d3d3d3;
  box-sizing: border-box;
  border-radius: 3px;
  background: #fff;
  margin-top: -12.5px;
}

input[type=checkbox]:checked + label::before {
  left: 0;
  background: #ea7404 url("../images/new/common/ico_checkbox.png") center center no-repeat;
  background-size: 13px 9px;
  border: none;
}

input[type=checkbox]:disabled + label {
  color: #aaa !important;
}

input[type=checkbox]:disabled + label::before {
  opacity: 0.6;
}

/* radio */
input[type=radio] + label {
  position: relative;
  display: inline-block;
  padding-left: 36px;
  font-size: 16px;
  min-height: 30px;
  color: #000;
  line-height: 26px;
  cursor: pointer;
}

input[type=radio] + label::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  width: 26px;
  height: 26px;
  border: 1px solid #ccc;
  box-sizing: border-box;
  border-radius: 50%;
  margin-top: -13px;
  background: #fff;
}

input[type=radio]:checked + label::before {
  background: url("../images/new/common/ico_radio.png") no-repeat;
  border: none;
}

input[type=radio]:disabled + label {
  color: #aaa;
}

input[type=radio]:disabled + label::before {
  opacity: 0.6;
}

/* wrapper & else */
.check_box,
.radio_box {
  display: inline-block;
  margin-right: 20px;
}

.check_box .sub,
.radio_box .sub {
  display: inline-block;
  font-size: 14px;
  color: #777;
  margin-left: 5px;
}

[data-category=CE] .form_check.line2 {
  text-align: left;
}

.form_check.solo label,
.form_radio.solo label {
  padding-left: 26px;
}
.form_check.solo input[type=radio] + label::before,
.form_radio.solo input[type=radio] + label::before {
  margin-top: -10px;
}

/* switcher */
.switcher {
  position: relative;
  display: inline-block;
}

.switcher input[type=checkbox] + label {
  position: relative;
  width: 60px;
  height: 30px;
  border-radius: 15px;
  background: #777;
  opacity: 1;
  vertical-align: top;
}

.switcher input[type=checkbox] + label:before {
  width: 26px;
  height: 26px;
  border-radius: 50%;
  border: 0;
  top: 2px;
  left: 2px;
  margin: 0;
  transition: left 0.4s;
  -webkit-transition: left 0.4s;
}

.switcher input[type=checkbox]:checked + label {
  background: #ea7404;
}

.switcher input[type=checkbox]:checked + label:before {
  left: 32px;
  background: #fff;
}

.switcher.s input[type=checkbox] + label {
  width: 46px;
  height: 24px;
  min-height: 24px;
}
.switcher.s input[type=checkbox] + label:before {
  width: 20px;
  height: 20px;
  padding: 0;
  min-height: 20px;
  background: #fff;
  border-radius: 50%;
  transition: left 0.4s;
  -webkit-transition: left 0.4s;
}
.switcher.s input[type=checkbox]:checked + label:before {
  left: 22px;
  padding: 0;
  min-height: 20px;
}

.switcher_label {
  color: #555;
  margin-right: 5px;
}

.switcher_label + .switcher {
  margin-top: 2px;
}

