/**
* Form Structure
* ------------------
* > .form_wrap
* >> 		gird_wrap
* >>> 		.form_group
* >>>>			.form_items
*/
/* gridWrap */
.grid_wrap {
  display: flex;
  flex: 1;
}
.grid_wrap::after {
  display: block;
  content: "";
  clear: both;
}

.grid_wrap + .grid_wrap {
  margin-left: 80px;
}

.grid_wrap.half {
  display: block;
  width: calc(50% - 40px);
  flex: none;
}

/* form_wrap */
.form_wrap {
  display: flex;
  flex: 1;
}
.form_wrap::after {
  display: block;
  content: "";
  clear: both;
}
.form_wrap + .form_wrap {
  margin-top: 15px;
}
.form_wrap .form_group:last-child {
  margin-right: 0;
}
.form_wrap .form_group:first-child {
  margin-left: 0;
}
.form_wrap .grid_wrap:last-child {
  margin-right: 0;
}
.form_wrap .grid_wrap:first-child {
  margin-left: 0;
}
.form_wrap.half .grid_wrap {
  width: calc(50% - 40px);
  margin-right: 40px;
}
.form_wrap.right {
  justify-content: flex-end;
}

