#skip {
  position: absolute;
  width: 100%;
  margin: 0 auto;
}

#skip a {
  text-indent: 0;
  background: #000;
  position: absolute;
  z-index: 1;
  top: -500px;
  width: 100%;
  padding: 5px 0;
  text-align: center;
}

#skip a:active,
#skip a:focus {
  top: 0;
  color: #fff;
}

