* [20230307: 수정]: responsive(1920, 1200) & subMenuAreaWrap
* ------------------
*/
/* GNB-Wrap /개선 /202005 */
.gnbWrapSet {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background: #ffffff;
  overflow: visible !important;
}

/* jQuery animate 시 overflow:hidden 방지 /202005 */
.gnbWrapSet.open {
  padding-bottom: 1px;
  border-bottom: 1px solid #555555;
  overflow: hidden !important;
} /* jQuery animate 시 overflow:hidden 방지 /202005 */
.gnbWrap {
  position: relative;
  border-bottom: 2px solid #ee5a00;
  background: #ffffff;
}

.gnbWrap.main_view {
  position: relative;
  border-bottom: 2px solid #003170;
  background: #ffffff;
} /* 메인 블루컬러 변경 */
.gnbWrapSet.open .gnbWrap .gnbArea .subMenuArea {
  border-top: 2px solid #ee5a00;
}

.gnbWrap .gnbArea {
  position: relative;
  margin: 0 auto;
  padding-top: 35px;
  max-width: 1920px;
  padding-left: 40px;
  padding-right: 40px;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
.gnbWrap .gnbArea:after {
  content: "";
  display: block;
  clear: both;
}

.gnbWrap .gnbArea .logo {
  float: left;
  position: relative;
  margin-top: 3px;
  width: 230px;
}

.gnbWrap .gnbArea .logo img.logo2 {
  position: absolute;
  left: 0;
  top: 0;
  display: none;
  z-index: 1;
}

.gnbWrap .gnbArea .topmenu {
  position: absolute;
  right: 40px;
  top: 9px;
}
.gnbWrap .gnbArea .topmenu .topmeuWrap {
  position: relative;
  max-width: 1920px;
}
.gnbWrap .gnbArea .topmenu .menu {
  overflow: hidden;
  margin-right: 15px;
  float: left;
}
.gnbWrap .gnbArea .topmenu .menu li {
  float: left;
  position: relative;
  padding-right: 8px;
  margin-right: 6px;
}
.gnbWrap .gnbArea .topmenu .menu li:after {
  display: block;
  content: "";
  position: absolute;
  right: 0;
  top: 50%;
  width: 1px;
  height: 12px;
  background: #eeeeee;
  margin-top: -6px;
}
.gnbWrap .gnbArea .topmenu .menu li:last-child {
  padding-right: 0;
  margin-right: 0;
}
.gnbWrap .gnbArea .topmenu .menu li:last-child:after {
  display: none;
