/**
* Module.motion
* ------------------
*/
.illust-link {
  z-index: 12;
  border: 1px solid #ed6b28;
  background: #ed6b28;
  animation: motion_bg 3200ms ease-out infinite;
}
.illust-link:after {
  border-top-color: #ed6b28;
  animation: motion_after 3200ms ease-out infinite;
  z-index: 3;
  bottom: -8px;
}
.illust-link:before {
  position: absolute;
  content: "";
  width: 0;
  height: 0;
  right: 14px;
  bottom: -10px;
  border-top: 9px solid #ed6b28;
  border-left: 5px solid transparent;
  border-right: 4px solid transparent;
  z-index: 1;
}

.txt-motion {
  overflow: hidden;
  position: relative;
  z-index: 10;
  width: 90px;
  height: 30px;
}
.txt-motion .text {
  color: #fff;
  font-size: 14px;
  font-weight: 700;
  display: inline-block;
  position: absolute;
  bottom: 6px;
  animation: motion 3200ms ease-out infinite;
}
.txt-motion .text-1 {
  left: 0;
  animation-delay: 0ms;
}
.txt-motion .text-2 {
  left: 12px;
  animation-delay: 120ms;
}
.txt-motion .text-3 {
  left: 24px;
  animation-delay: 200ms;
}
.txt-motion .text-4 {
  left: 36px;
  animation-delay: 280ms;
}
.txt-motion .text-5 {
  left: 48px;
  animation-delay: 330ms;
}
.txt-motion .text-6 {
  left: 64px;
  animation-delay: 380ms;
}
.txt-motion .text-7 {
  left: 76px;
  animation-delay: 420ms;
}

@keyframes motion {
  50% {
    color: #fff;
  }
  68% {
    color: #ed6b28;
  }
  76% {
    color: #fff;
  }
}
@keyframes motion_bg {
  50% {
    background: #f95501;
  }
  70% {
    background: #fff;
  }
  85% {
    background: #f95501;
  }
}
@keyframes motion_after {
  50% {
    border-top-color: #f95501;
  }
  70% {
    border-top-color: #fff;
  }
  85% {
    border-top-color: #f95501;
  }
}
.illust-link-low {
  z-index: 12;
  right: 40px;
  top: 0;
  font-size: 14px;
  line-height: 20px;
  font-weight: 700;
  padding: 0 9px;
  color: #fff;
  position: absolute;
  height: 30px;
  background: #ed6b28;
  border-radius: 5px;
  box-shadow: 3px 3px 6px rgba(0, 0, 0, 0.1);
}
.illust-link-low:after {
  position: absolute;
  content: "";
  width: 0;
  height: 0;
  right: 9px;
  bottom: -9px;
  border-top: 18px solid #ed6b28;
  border-left: 9px solid transparent;
  border-right: 9px solid transparent;
}

